import { createStore } from "vuex";

export default createStore({
	state: () => {
		return {
			tweets: [],
		};
	},
	getters: {
		tweets: (state) => {
			return state.tweets;
		},
		hasTweets: (state) => {
			return state.tweets && state.tweets.length > 0;
		},
	},
	mutations: {
		loadTweets: (state, payload) => {
			state.tweets = payload;
			// console.log(state.tweets.length);
		},
		addNewTweet: (state, payload) => {
			// const tweets = [...state.tweets];
			// tweets.unshift(payload);
			// state.tweets = tweets;

			state.tweets.unshift(payload);
			// console.log(state.tweets);
			// console.log(state.tweets.length);
		},
	},
	actions: {
		addNew: (ctx, payload) => {
			// const endpoint = "/tweet/create";

			const newTweet = {
				id: payload.id,
				content: payload.content,
				likes: payload.likes,
				is_retweet: payload.is_retweet,
				parent: payload.parent,
			};

			ctx.commit("addNewTweet", newTweet);
		},

		setTweets: async (ctx) => {
			const url = "http://localhost:8000/api/tweets",
				response = await fetch(url),
				data = await response.json(),
				allTweets = [];

			if (!response.ok) {
				throw new Error(response.message || "Unable to fetch");
			}

			for (const i in data) {
				const tweet = {
					id: data[i].id,
					content: data[i].content,
					likes: data[i].likes,
					is_retweet: data[i].is_retweet,
					parent: data[i].parent,
				};
				allTweets.push(tweet);
			}

			ctx.commit("loadTweets", allTweets);
		},
	},
	modules: {},
});
